body {
  margin: 0;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #000;
  color: #fff;
}

html, body, #root {
  width: 100%;
  height: 100%;
  max-width: 100%;
  overflow-x: hidden;
  -webkit-text-size-adjust: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Scrollbar Styling */
::-webkit-scrollbar {
  width: 12px;
}

::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.5);
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(180deg, #fb7185 0%, #f59e0b 100%);
  border-radius: 6px;
  border: 2px solid rgba(0, 0, 0, 0.5);
}

::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(180deg, #f59e0b 0%, #fb7185 100%);
  box-shadow: 0 0 10px rgba(251, 113, 133, 0.35);
}

/* Links */
a {
  color: #fb7185;
  text-decoration: none;
  transition: all 0.3s cubic-bezier(0.34, 1.56, 0.64, 1);
}

a:hover {
  color: #f59e0b;
  text-shadow: 0 0 10px rgba(245, 158, 11, 0.3);
}

/* Selection */
::selection {
  background: linear-gradient(135deg, #fb7185 0%, #f59e0b 100%);
  color: #000;
  text-shadow: none;
}

::-moz-selection {
  background: linear-gradient(135deg, #fb7185 0%, #f59e0b 100%);
  color: #000;
}
